<template>
  <aside class="filter">
    <h2 class="filter__title">Фильтры</h2>
    <form class="filter__form form" action="#" method="get" @submit.prevent="submit">
      <BaseFilterField title="Цена:" label-class="form__label--price">
        <label class="form__label form__label--price">
          <input class="form__input" type="text" name="min-price" v-model.number="current.minPrice">
          <span class="form__value">От</span>
        </label>
        <label class="form__label form__label--price">
          <input class="form__input" type="text" name="max-price" v-model.number="current.maxPrice">
          <span class="form__value">До</span>
        </label>
      </BaseFilterField>
      <BaseFilterField title="Категория:" :error="error.category">
        <label class="form__label form__label--select">
          <select class="form__select" type="text" name="category" v-model.number="current.categoryId">
            <option value="0">Все категории</option>
            <option :value="category.id" v-for="category in computedCategories" :key="category.id">{{category.title}}</option>
          </select>
        </label>
      </BaseFilterField>

      <BaseFilterField title="Цвет:" :error="error.price">
        <ColorList :colors-arr="colorsData" title="Цвет:" :selected-color-id.sync="current.colorId"/>
      </BaseFilterField>

      <BaseFilterField :title="computedPropsArr[0].title+':'" v-if="categoryData && computedPropsArr[0]">
        <ul class="check-list">
          <li class="check-list__item" v-for="subItem in computedPropsArr[0].availableValues" :key="subItem.value">
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" :id="subItem.value" :value="subItem.value" v-model="selectedPropValues0">
                <span class="check-list__desc">
                  {{subItem.value}}
                  <span>({{subItem.productsCount}})</span>
                </span>
            </label>
          </li>
        </ul>
      </BaseFilterField>

      <BaseFilterField :title="computedPropsArr[1].title+':'" v-if="categoryData && computedPropsArr[1]">
        <ul class="check-list">
          <li class="check-list__item" v-for="subItem in computedPropsArr[1].availableValues" :key="subItem.value">
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" :id="subItem.value" :value="subItem.value" v-model="selectedPropValues1">
                <span class="check-list__desc">
                  {{subItem.value}}
                  <span>({{subItem.productsCount}})</span>
                </span>
            </label>
          </li>
        </ul>
      </BaseFilterField>

<!--      <div style="padding:20px 0; font-size: 12px; color:red">-->
<!--        <div style="margin-bottom:10px">final obj for request:</div>-->
<!--        <div>categoryId: {{current.categoryId}}</div>-->
<!--        <div>minPrice: {{current.minPrice}}</div>-->
<!--        <div>maxPrice: {{current.maxPrice}}</div>-->
<!--        <div>props: {{computedPropsObjForRequest}}</div>-->
<!--      </div>-->

      <button class="filter__submit button button--primery" type="submit">
        Применить
      </button>
      <button class="filter__reset button button--second" type="button" @click.prevent="reset">
        Сбросить
      </button>
    </form>
  </aside>
</template>

<script>

  import axios from "axios";
  import {API_BASE_URL} from "../../config";
  import ColorList from "../common/ColorList";
  import BaseFilterField from "../common/BaseFilterField";
  import PropList from "../common/PropList";

  export default {
  components: {PropList, BaseFilterField, ColorList},
  data() {
    return {
      current: {//внутреннее состояние компонента чтобы потом 1 кнопкой "Применить" отправить запрос.
        categoryId: 0,
        colorId: undefined,
        minPrice: undefined,
        maxPrice: undefined,
      },
      selectedPropValues0: [],
      selectedPropValues1: [],
      categoriesArr: null,//при первоначальной загрузке комплнента - запрос и запись списка категорий
      categoryData: null,//при изменении категории - запрос и запись данных этой категории
      colorsData: null,//при загрузке компонента запрос и запись в стейт
      error:{
        category: null,
        memory: null,
        price: null,
      }
    }
  },
  computed: {
    computedCategories() {
      return this.categoriesArr ? this.categoriesArr :[];//решение проблемы первонач рендера ([]).
    },
    computedPropsArr(){
      if(this.current.categoryId===0){
        return []
      }else if(this.categoryData){
        return this.categoryData.productProps;
      }
    },
    computedTextColor() {
      if(this.current.colorId){
        return this.colorsData.find(item=>item.id===this.current.colorId).title
      }
    },
    computedKey0(){
      if(this.categoryData && this.categoryData.productProps.length>0){
        return this.categoryData.productProps[0].code;
      }
    },
    computedKey1(){
      if(this.categoryData && this.categoryData.productProps.length>1){
        return this.categoryData.productProps[1].code;
      }
    },
    computedPropsObjForRequest() {
      let finalPropObj;
      if(this.current.colorId){
        finalPropObj = {
          ['color'] : [this.computedTextColor],
        }
      }
      if(this.selectedPropValues0.length!==0){//если выделен первый уровень ключей
        finalPropObj = {
          ...finalPropObj,
          [this.computedKey0] : this.selectedPropValues0,
        }
      }
      if(this.selectedPropValues1.length!==0){//если выделен второй уровень ключей
        finalPropObj = {
          ...finalPropObj,
          [this.computedKey1] : this.selectedPropValues1,
        }
      }
      return finalPropObj
    }
  },
  methods: {
    submit() {
      this.$store.commit('updateFilter', {
        categoryId: this.current.categoryId,
        minPrice: this.current.minPrice,
        maxPrice: this.current.maxPrice,
        props: this.computedPropsObjForRequest,
      })
    },
    reset() {
      this.current.categoryId = 0;
      this.current.colorId = undefined;
      this.current.minPrice = undefined;
      this.current.maxPrice = undefined;
      this.selectedPropValues0 = [];
      this.selectedPropValues1 = [];
      this.submit()
    },
    loadCategories() {//загрузка готовых категорий с сервера и установка в локал стейт
      axios.get(API_BASE_URL + '/api/productCategories')
        .then(res => this.categoriesArr = res.data.items)
    },
    loadCategory(id) {
      this.selectedPropValues0 = [];//сброс выделенных итемов
      this.selectedPropValues1 = [];
      axios.get(API_BASE_URL + '/api/productCategories' + `/${id}`)
        .then(res => this.categoryData = res.data)
    },
    loadColors() {//загрузка готовых цветов с сервера и установка в локал стейт
      axios.get(API_BASE_URL + '/api/colors')
        .then(res => this.colorsData = res.data.items)
    },
  },
  created() {//при рендере компонента, сразу вызывается загрузка категорий и цветов
    this.loadCategories();
    this.loadColors();
  },
  watch: {
    'current.categoryId': {
      handler: function () {
        if(this.current.categoryId!==0){
          this.loadCategory(this.current.categoryId)
        }
      },
    },
  },

};
</script>

