<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.productOffer.product.preview.file.url" width="120" height="120" alt="item.product.title">
    </div>
    <h3 class="product__title">
      {{item.productOffer.product.mainProp.code==='color'?item.productOffer.product.title:item.productOffer.title}}
    </h3>
    <p class="product__info product__info--color" v-if="item.productOffer.product.mainProp.title==='Цвет'">
      Цвет:
      <span>
        <i :style='{backgroundColor: `${item.color.color.code}`}'></i>
        {{item.color.color.title}}
      </span>
    </p>
    <p class="product__info" v-if="item.productOffer.product.mainProp.title!=='Цвет'">
      {{item.productOffer.product.mainProp.title}}: <span v-for="i in item.productOffer.propValues" :key="i.value">{{i.value}}</span>
    </p>


    <span class="product__code">
      Артикул: {{item.id}}
    </span>
    <Counter :amount.sync="amount"/>

    <b class="product__price">
      {{(item.price * item.quantity) | numberFormat}} ₽
    </b>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="deleteProduct(item.id)">
      <svg id="icon-close" enable-background="new 0 0 512 512" height="20" viewBox="0 0 512 512" width="20" xmlns="http://www.w3.org/2000/svg">
        <g>
          <path d="m424 64h-88v-16c0-26.467-21.533-48-48-48h-64c-26.467 0-48 21.533-48 48v16h-88c-22.056 0-40 17.944-40 40v56c0 8.836 7.164 16 16 16h8.744l13.823 290.283c1.221 25.636 22.281 45.717 47.945 45.717h242.976c25.665 0 46.725-20.081 47.945-45.717l13.823-290.283h8.744c8.836 0 16-7.164 16-16v-56c0-22.056-17.944-40-40-40zm-216-16c0-8.822 7.178-16 16-16h64c8.822 0 16 7.178 16 16v16h-96zm-128 56c0-4.411 3.589-8 8-8h336c4.411 0 8 3.589 8 8v40c-4.931 0-331.567 0-352 0zm313.469 360.761c-.407 8.545-7.427 15.239-15.981 15.239h-242.976c-8.555 0-15.575-6.694-15.981-15.239l-13.751-288.761h302.44z"/>
          <path d="m256 448c8.836 0 16-7.164 16-16v-208c0-8.836-7.164-16-16-16s-16 7.164-16 16v208c0 8.836 7.163 16 16 16z"/>
          <path d="m336 448c8.836 0 16-7.164 16-16v-208c0-8.836-7.164-16-16-16s-16 7.164-16 16v208c0 8.836 7.163 16 16 16z"/>
          <path d="m176 448c8.836 0 16-7.164 16-16v-208c0-8.836-7.164-16-16-16s-16 7.164-16 16v208c0 8.836 7.163 16 16 16z"/>
        </g>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from "../../helpers/numberFormat";
import {mapActions} from "vuex";
import Counter from "../common/Counter";

export default {
  components: {Counter},
  filters: {// html -> {{product.price | numberFormat}} ₽ - значение слева передается аргументом в правую функцию. Фича Vue. Либо аналогом computed. А если просто html {{numberFormat(value)}} то б ошибка на нереактивность.
    numberFormat,
  },
  props: ['item'],
  computed: {
    amount: {//изменение инпута через стор с запросом.
      get(){
        return this.item.quantity;
      },
      set(value){//изменение количества товара в итеме путем dispatch action.
        this.$store.dispatch('updateCartProductAmountAction', {basketItemId:this.item.id, quantity: value});//вариант напрямую без mapActions
      }
    }
  },
  methods:{
    ...mapActions(['deleteProductFromCartAction', 'changeProductAmountAction', 'updateCartProductAmountAction']),
    deleteProduct() {//метод инициирующий экшн кот сделает запрос на сервер об удалении товара из корзины.
      this.deleteProductFromCartAction({productId:this.item.id})
    },
  }
};
</script>
