import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import {API_BASE_URL} from "../config";

Vue.use(Vuex);

export const moduleCart = {
  state: {
    cart:[],
    cartData:[],
    isCartLoading:false,
    isCartLoadingFailed:false,
    isDeleting:false,
    isDeletingFailed:false,
  },
  mutations: {//изменение стейта/стора мутацией а не нов копией как в React. Правило стора. Сохраняет историю, удобно при отладке. Всегда синхронные.
    resetLocalCart(state){
      state.cart = [];
      state.cartData = [];
    },
    updateCartProductAmount(state, payload){
      const existingItems = state.cart.find(item=>item.productId === payload.productId);
      if (existingItems){
        existingItems.amount = payload.amount;
      }
    },
    updateCartData(state, items){
      state.cartData = items;
    },
    updateIsCartLoading(state, bool){
      state.isCartLoading = bool;
    },
    updateIsCartLoadingFailed(state, bool){
      state.isCartLoadingFailed = bool;
    },
  },
  getters: {
    cartData(state){
      return state.cartData;
    }
  },
  actions: {
    loadCart({ dispatch, commit, getters, rootGetters }){
      commit('updateIsCartLoading', true);
      return axios
        .get(`${API_BASE_URL}/api/baskets`, {
          params: {
            userAccessKey: getters.userAccessKey
          },
        })
        .then(res => {
          console.log('load cart from server:',res)
          if(!getters.userAccessKey){//если в сторе еще нет ключа то...
            localStorage.setItem('userAccessKey', res.data.user.accessKey);//...записываем его в локал для дальн использования...
            commit('updateUserAccessKey',res.data.user.accessKey);//...и мутируем. Первая загрузка и установка ключа в стейт. И при последующих запросах сохраненный ключ передается через context. При закрытой вкладке все удаляется, поэтому доп-но использ localStorage.
          }
          commit('updateCartData', res.data.items);//обновляем корзину мутацией...
        })
        .catch(()=>{
          commit('updateIsCartLoadingFailed', true);
        })
        .then(()=>commit('updateIsCartLoading', false))
    },
    addProductToCart({ dispatch, commit, getters, rootGetters }, {productOfferId,quantity,colorId}){//2м арг добавляется как и в мутации payload.
      return (new Promise(resolve => setTimeout(resolve,2000)))//искуств предействие для любого action (исскусств задержка чтобы посмореть статусы отправки, для дебага)
        .then(()=>{
          console.log('adding to cart:',productOfferId,quantity,colorId)
          return axios
            .post(`${API_BASE_URL}/api/baskets/products`, {//нужные данные кот просит сервер
              productOfferId: productOfferId,
              colorId: colorId,
              quantity : quantity,
            },{
              params:{
                userAccessKey: rootGetters.userAccessKey//при рендере Арр создано новое или взятое с локала.
              }
            })
            .then(res => {//в ответ приходит обновленная вся корзина
              commit('updateCartData', res.data.items);//обновляем корзину через мутацию...
            })
        })
    },
    changeProductAmountAction({ dispatch, commit, getters, rootGetters }, {productId,amount}){
      console.log('changeProductAmountAction')
      return axios
        .put(`${API_BASE_URL}/api/baskets/products`, {
          productId: productId,
          quantity : amount,
        },{
          params:{
            userAccessKey: rootGetters.userAccessKey
          }
        })
        .then(res => {
          commit('updateCartData', res.data.items);//обновляем корзину через мутацию...
          // commit('syncCartProducts');//...и затем, после синхронного обновления корзины, переписываем приходящ данные под свой формат.
        })
    },
    deleteProductFromCartAction({ dispatch, commit, getters, rootGetters }, {productId}){//2м арг добавляется как и в мутации payload.
      return axios
        .delete(`${API_BASE_URL}/api/baskets/products`, {
          data:{
            basketItemId: productId,
          },
          params:{
            userAccessKey: rootGetters.userAccessKey
          },
        })
        .then(res => {
          commit('updateCartData', res.data.items);//обновляем корзину через мутацию...
        })
    },
    updateCartProductAmountAction({ dispatch, commit, getters, rootGetters }, {basketItemId,quantity}){//схоже с addProductToCart
      commit('updateCartProductAmount', {basketItemId,quantity});//не дожидаясь ответа от сервера чтобы отобразить нужное число делаем сразу мутацию. Так быстрее.
      if(quantity<1){//исправление ошибки невозможность набрать пустое поле
        return;
      }
      return axios
        .put(`${API_BASE_URL}/api/baskets/products`, {
          basketItemId:basketItemId,
          quantity:quantity,
        },{
          params:{
            userAccessKey: rootGetters.userAccessKey
          }
        })
        .then(res => {
          commit('updateCartData', res.data.items);
        })
        .catch(()=>{//в случае ошибки или ввода ошибочного числа происходит откат (предыд знач поля)
          // commit('syncCartProducts');
        })

    },
  }
}
