import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export const moduleUser = {
  state: {
    userAccessKey: null,
  },
  mutations: {
    updateUserAccessKey(state, accessKey){
      state.userAccessKey = accessKey;
    },
  },
  getters: {
    userAccessKey(state) {
      return state.userAccessKey
    },
  },
}
