import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import {API_BASE_URL} from "../config";
import Qs from "qs";

Vue.use(Vuex);

export const moduleFilter = {
  state: {
    filter: {
      minPrice: undefined,
      maxPrice: undefined,
      categoryId: undefined,
      props:undefined,
      page: 1,
      productsPerPage: 12,
    },
  },
  mutations: {
    updateFilter(state, payload){
      state.filter.minPrice = payload.minPrice
      state.filter.maxPrice = payload.maxPrice
      state.filter.categoryId = payload.categoryId
      state.filter.props = payload.props
    },
  },
  getters: {
    filter(state){
      return state.filter
    },
  },
  actions: {
    loadProductAction(context, payload){
      console.log(`${API_BASE_URL}/api/products/${payload.productId}`)
      return axios.get(`${API_BASE_URL}/api/products/${payload.productId}`)
    },
    loadProductsAction(context){
      console.log('loadProductsAction:',context.state.filter)
      return axios.get(`${API_BASE_URL}/api/products`, {
        params:  {//апи просит эти параметры.
          categoryId: context.state.filter.categoryId,
          page: context.state.filter.page,
          limit: context.state.filter.productsPerPage,
          minPrice: context.state.filter.minPrice,
          maxPrice: context.state.filter.maxPrice,
          props: context.state.filter.props,
        },
        paramsSerializer: function (params) {
          return Qs.stringify(params, {arrayFormat: 'brackets'})
        },
      })
    },
  },
}
